:root {
  --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  --animation-duration: 200ms;
}

.team-container {
  display: grid;
  max-width: 1140px;
  grid-template-columns: repeat(auto-fit, minmax(170px, 1fr));
  grid-auto-rows: minmax(260px, auto);
  grid-gap: 12px;
  justify-content: center;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
}

.team__item {
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  cursor: pointer;
  padding: 10px;
  perspective: 600px;
  text-align: center;
  background-color:   rgba(245, 124, 50, 0.12);
}

.team__item:hover {
  transform: scale(1.05);
  transition: transform var(--animation-duration) var(--timing-function);
  box-shadow: 0px 4px 4px rgba(245, 124, 50, 0.12), 0px 4px 4px rgba(245, 124, 50, 0.12),
  1px 4px 6px rgba(245, 124, 50, 0.12);
}

.team__image {
  min-width: 0;
  height: 150px;
  width: 150px;
  border-radius: 50%;
  object-fit: cover;
  margin: 10px auto 0;
}

.team-meta {
  padding: 10px;
  font-weight: 700;
}

.team-meta p {
  margin-top: 0;
  margin-bottom: 0;
  letter-spacing: 0.1em;
}

.front,
.back {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  transition: 1s;
  backface-visibility: hidden;
  border-radius: 10px;
}

.front {
  background: #ffffff;  
}

.back {
  padding: 20px;
  background-color: #f5f3f3;
  transform: rotateY(180deg);
}

.clicked .front {transform: rotateY(360deg);}
.clicked .back {transform: rotateY(360deg);}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
}
    
.modal {
  position: relative;
  padding: 40px;
  background-color: #b8b4a7;
  box-shadow: 0px 4px 4px rgba(245, 124, 50, 0.12), 0px 4px 4px rgba(245, 124, 50, 0.12),
  1px 4px 6px rgba(245, 124, 50, 0.12);
  border-radius: 4px;
  transition: transform 550ms cubic-bezier(0.4, 0, 0.2, 1);
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-widht: 768px) {
      position: absolute;
      width: 760px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(1.1);
  }

  @media screen and (min-width: 1200px) { 
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(1.1);
  }
}

 .modal__icon {
  padding: 0;
  width: 18px;
  height: 18px;
  fill: #ff6b08;
}

.modal__icon:hover {
  transform: scale(1.3);
}

.modal__button {
  position: absolute;
  right: 8px;
  top: 8px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 1px solid rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
  transition: fill 550ms cubic-bezier(0.4, 0, 0.2, 1);
} 

.backdrop {
  padding: 20px;
  overflow-y: scroll;
  opacity: 1;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.2);
  position: fixed;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0.8);
}

body.modal-open {
  overflow: hidden;
}

.team__item-title {
  text-transform: uppercase;
  color: #ff6b08;
  line-height: 1.14;
  font-weight: 500;
  margin-bottom: 8px;
}

.team__item-text {
  font-size: 14px;
  color: #212121;
  font-weight: 400;
  line-height: 1.14;
  margin-bottom: 10px;
}
